<template>
    <div ref="collapse" :style="{ height: collapse ? '' : getHeight() }" class="collapse">
        <p ref="text" class="collapse__text">
            <slot></slot>
        </p>
        <span ref="expand" class="expand">
            <a @click.stop.prevent="collapse = !collapse" href="#" class="expand__link">Развернуть текст</a>
        </span>
    </div>
</template>

<script>
export default {
  data () {
    return {
      collapse: true
    }
  },
  methods: {
    getHeight: function () {
      return this.$refs.text.offsetHeight + this.$refs.expand.offsetHeight + 'px'
    }
  }
}
</script>

<style lang="stylus" scoped>
.collapse
    position relative
    overflow hidden
    height 45px
    transition all ease 0.3s
    &__text
      position relative
      margin 0
      color #515151
      font-weight 400
      line-height 21px
.expand
    position absolute
    bottom 0
    right 0
    padding 4px 0 4px 70px
    background linear-gradient(90deg, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 20%, rgba(255,255,255,1) 100%)
    &__link
      border-bottom 1px dashed #789d44
      text-decoration none
      color #789d44
      font-size 13px
      font-weight 400
      line-height 13px
</style>
